import React, { PureComponent } from 'react'
import { Helmet } from 'react-helmet'
import Container from 'Shared/components/Container'
import './Privacy.scss'
import { privacyPage } from 'Shared/constants/MetaTags'

class Privacy extends PureComponent {
  render () {
    return (
      <div className='privacy__container parsing--hidden'>
        <Helmet>
          <title>{privacyPage.title}</title>
          <meta name='description' content={privacyPage.description} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={privacyPage.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={privacyPage.url} />
          <meta property='og:title' content={privacyPage.title} />
          <meta property='og:description' content={privacyPage.description} />
        </Helmet>
        <Container>
          <h1 className='privacy__header'>Privacy Policy of Oliv LCC<br /><span className='privacy__header--update-date'>Updated September 2018</span></h1>
          <div className='columns'>
            <div className='column col-9 col-mx-auto col-xs-12'>
              <div>
                <div>
                  Oliv LLC (referred to as “<strong>we​</strong>”, “<strong>our​</strong>” or “<strong>us​</strong>”
                  in this privacy policy) which owns and operates the website <a href='/'>www.oliv.com</a> (the
                  “<strong>Website​</strong>”) is committed to protecting and respecting your privacy. Please read this
                  privacy policy carefully, as it (together with our terms of use and any other documents referred to in
                  it) sets out what personal data we collect, how we collect and process it and the purpose for which we
                  collect and use this data. It also explains your rights in relation to your personal data.
                </div>
                <br />
                <div>
                  This privacy policy relates to personal data that identifies you as a natural person (whether you
                  are an actual or potential user of our service, an individual who browses our Website or an
                  individual outside our organisation with whom we interact).
                </div>
                <br />
                <div>
                  The privacy and security of your personal data is very important to us so we want to assure you
                  that your information will be properly managed and protected by us at all times.
                </div>
              </div>

              <div className='privacy__ordered-list'>
                <ol className='privacy__ordered-list--level-1'>
                  <li>
                    <h2 className='privacy__subtitle'>What information do we collect?</h2>
                    <div className='privacy__paragraph'>We may collect the following personal data about you:</div>
                    <ol className='privacy__ordered-list--level-2'>
                      <li>
                        <strong>Information you give us</strong>
                        <table className='privacy__table'>
                          <tr>
                            <th>What personal data might be supplied to us?</th>
                            <th>Why we process that personal data:</th>
                            <th>The legal basis for processing that personal data.</th>
                          </tr>
                          <tr>
                            <td>
                              Information about you provided by you when you register for an account, use the Website or
                              report a problem about the service to us, including your full name, email address, phone
                              number and location.
                            </td>
                            <td>
                              We process this data to verify your identity, deal with your registration, manage your
                              account with us, to provide our services, to communicate with you and to back up our
                              database.
                            </td>
                            <td>
                              The legal basis we rely on for processing this data is as follows: (a) our legitimate
                              interests; or (b) fulfilment of our contract with you; or (c) consent.
                            </td>
                          </tr>
                          <tr>
                            <td>
                              Documents, photos, videos and written profile content uploaded by you to your online
                              profile.
                            </td>
                            <td>
                              We process this data to maintain your account with us and to provide our services.
                            </td>
                            <td>
                              The legal basis we rely on for processing this data is as follows: (a) our legitimate
                              interests; or (b) consent.
                            </td>
                          </tr>
                          <tr>
                            <td>
                              Conversations that you have with other users on the Website through Twilio’s chat
                              functionality.
                            </td>
                            <td>
                              We process this data to maintain your account with us and to provide our services
                            </td>
                            <td>
                              The legal basis we rely on for processing this data is as follows: (a) our legitimate
                              interests; (b) consent.
                            </td>
                          </tr>
                          <tr>
                            <td>
                              Conversations you have with our staff through Intercom’s chat functionality.
                            </td>
                            <td>
                              We process this data to maintain your account with us and to provide our services,
                              specifically informing you about the functionality of our software and/or selling our
                              software to you
                            </td>
                            <td>
                              The legal basis we rely on for processing this data is as follows: (a) our legitimate
                              interests; or (b) fulfilment of our contract with you or (c) consent.
                            </td>
                          </tr>
                          <tr>
                            <td>
                              Where you subscribe to receive our email notifications and/or newsletters, we will process
                              your data so that we can send such email notifications and/or newsletters to you.
                            </td>
                            <td>
                              We will only process such personal data where you have consented to us doing so.
                            </td>
                            <td>
                              The legal basis we rely on for processing this data is as follows (a) consent.
                            </td>
                          </tr>
                        </table>
                      </li>
                      <li>
                        <strong>Information we collect from other sources</strong>
                        <table className='privacy__table'>
                          <tr>
                            <th>
                              What personal data might we collect from other sources?
                            </th>
                            <th>
                              Why we process such personal data:
                            </th>
                            <th>
                              The legal basis for processing this personal data.
                            </th>
                          </tr>
                          <tr>
                            <td>
                              Technical information relating to your use of our Website, including (a) IP address used
                              to connect your computer to the internet, your login information, browser type and
                              version, time zone setting, browser plug-in types and versions, operating system and
                              platform; and (b) information about your visit to our Website, including the full Uniform
                              Resource Locators (URL), clickstream to, through and from our Website (including date and
                              time), services you viewed or searched for, page response times, download errors, length
                              of visits to certain pages, page interaction information (such as scrolling, clicks, and
                              mouse-overs), methods used to browse away from the page.
                            </td>
                            <td>
                              We process this data so we can monitor and analyse how the Website is used so we can
                              improve our Website and our services. This helps us to administer our business and improve
                              the services we offer.
                            </td>
                            <td>
                              The legal basis we rely on for processing this data is as follows: (a) our legitimate
                              interests.
                            </td>
                          </tr>
                          <tr>
                            <td>
                              Third Party Data which we may receive about you from organisations we work with such as
                              sub-contractors in technical, payment and delivery services, advertising networks,
                              analytics providers, search information providers, credit reference agencies. Those
                              organisations will have their own privacy policies detailing how they process personal
                              data.
                            </td>
                            <td>
                              We process this data to maintain your account with us and to provide our services.
                            </td>
                            <td>
                              The legal basis we rely on for processing this data is as follows: (a) our legitimate
                              interests.
                            </td>
                          </tr>
                        </table>
                      </li>
                      <li>
                        <strong>Financial information​</strong> - ​NONE of your credit/debit card information is stored on the
                        Website. Your payment information on the Website are collected and
                        processed through the payment gateway solution provided by Telr. We have no
                        responsibility for this and the terms of Telr’s privacy policy will apply.
                      </li>
                    </ol>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>How we use the information we collect:</h2>
                    <ol className='privacy__ordered-list--level-2'>
                      <li>
                        We use information that we collect about you to:
                        <ol className='privacy__ordered-list--level-3'>
                          <li>deliver the products and services that you have requested;</li>
                          <li>manage your account and provide you with customer support;</li>
                          <li>
                            perform research and analysis about your use of, or interest in, our products,
                            services, or content, or products
                          </li>
                          <li>
                            communicate with you by email, telephone and/or mobile devices about products or
                            services that may be of interest to you either from us, our sister companies or other
                            third parties;
                          </li>
                          <li>
                            develop and display content and advertising tailored to your interests on our
                            Website;
                          </li>
                          <li>
                            enforce our terms and conditions;
                          </li>
                          <li>
                            manage our business; and
                          </li>
                          <li>
                            perform functions as otherwise described to you at the time of collection
                          </li>
                        </ol>
                      </li>
                    </ol>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>With whom we share your information:</h2>
                    <ol className='privacy__ordered-list--level-2'>
                      <li>
                        We will not share your personal data with third parties unless it is needed in order to
                        provide the requested service to you, or when we have a legal obligation to do so. Any
                        third parties will process your personal data in conformance with this statement.
                      </li>
                      <li>
                        We want you to understand when and with whom we may share personal or other
                        information we have collected about you or your activities on our Website or while using
                        our services.
                      </li>
                      <li>
                        We do not share your personal data with others except as indicated below or when we
                        inform you and give you an opportunity to opt out of having your personal data shared.
                        We may share personal data with:
                        <ol className='privacy__ordered-list--level-3'>
                          <li>
                            <strong>Authorized service providers:​</strong> We may share your personal data with our authorized
                            service providers that perform certain services on our behalf. These services may
                            include processing credit and debit card payments, providing customer service and
                            marketing assistance, performing business and sales analysis, supporting our
                            Website functionality, and supporting contests, surveys and other features offered
                            through our Website. These service providers may have access to personal data
                            needed to perform their functions but are not permitted to share or use such
                            information for any other purposes. This includes the following in relation to the
                            information you provide to us:
                            <ol className='privacy__ordered-list--level-4'>
                              <li>
                                Documents, photos and written profile content uploaded by you to your
                                online profile. These files are stored on Amazon Web Services with the server
                                instance located in Frankfurt, Germany and headquarters in the United States
                                of America, and adheres to the EU-U.S. Privacy Shield Framework.
                              </li>
                              <li>
                                Videos uploaded to your profile by you. These videos are stored on Vimeo
                                and accessible only to users registered on the Oliv domain. Videos uploaded
                                to your profile on the Oliv platform by you are now stored on YouTube as
                                unlisted videos.
                              </li>
                              <li>
                                Conversations that you have with other users on the Website through Twilio’s
                                chat functionality. This chat functionality is provided by Twilio, which is based
                                in the United States of America, and adheres to the EU-U.S. Privacy Shield
                                Framework.
                              </li>
                              <li>
                                Conversations you have with our staff through Intercom’s chat functionality.
                                This chat functionality is provided by Intercom, which is based in the United
                                States of America, and adheres to the EU-U.S. Privacy Shield Framework.
                              </li>
                            </ol>
                          </li>
                          <li>
                            <strong>Business partners</strong>:​ When you engage in opportunities or promotions offered through
                            our Website or our services, we may share personal data with the businesses with
                            which we partnered with to offer you those products, services, promotions, or
                            contests. When you volunteer to engage in a particular merchant’s offer or program,
                            you authorize us to provide your email address and other information to that
                            merchant.
                          </li>
                          <li>
                            <strong>Employers</strong>:​ Employers who are active members of the Website are able to access
                            any approved candidate’s personal data only for the purpose of contacting them for
                            a job opportunity. Employers are responsible for any information that is downloaded
                            and taken out of the Website for the purpose of hiring. We do not take responsibility
                            for any personal data that the Employer chooses to retain off the Website.
                          </li>
                          <li>
                            <strong>Legal or regulatory reasons</strong>:​ We may disclose your information in response to a
                            subpoena or similar investigative demand, a court order, or a request for cooperation
                            from a law enforcement or other government agency; to establish or exercise our
                            legal rights; to defend against legal claims; or as otherwise required by law. In such
                            cases, we may raise or waive any legal objection or right available to us, in our sole
                            discretion. We also may disclose your information when we believe disclosure is
                            appropriate in connection with efforts to investigate, prevent, report or take other
                            action regarding illegal activity, suspected fraud or other wrongdoing; to protect and
                            defend the rights, property or safety of our company, our users, our employees, or
                            others; to comply with applicable law or cooperate with law enforcement; or to
                            enforce our Website terms and conditions or other agreements or policies.
                          </li>
                          <li>
                            <strong>Other situations</strong>:​ We also may disclose your information, in connection with a
                            substantial corporate transaction, such as the sale of our business, a divestiture,
                            merger, consolidation, or asset sale, or in the unlikely event of bankruptcy.
                          </li>
                          <li>
                            Any third parties to whom we may disclose personal data (including those identified
                            in this section 3.3) may have their own privacy policies which describe how they use
                            and disclose personal data. Those policies will govern use, handling and disclosure
                            of your personal data once we have shared it with those third parties as described in
                            this privacy policy. If you want to learn more about their privacy practices, we
                            encourage you to visit their websites. These entities or their servers may be located
                            either inside or outside the United Arab Emirates.
                          </li>
                        </ol>
                      </li>
                      <li>
                        <strong>Aggregated and non-personal data​</strong>.​ We may share aggregated and non-personal data
                        we collect under any of the above circumstances. We may also share it with third parties
                        to develop and deliver targeted advertising on our Website and on websites of third
                        parties. We may combine non-personal data we collect with additional non-personal data
                        collected from other sources. We also may share aggregated information with third
                        parties, including advisors, advertisers and investors, for the purpose of conducting
                        general business analysis. For example, we may tell our investors the number of visitors
                        to our Website and the most popular features or services accessed. This information
                        does not contain any personal data and may be used to develop web content and
                        services that we hope you and other users will find of interest and to target content and
                        advertising.
                      </li>
                      <li>
                        Unfortunately, the transmission of information via the internet is not completely secure.
                        Although we will do our best to protect your personal data, and we will maintain
                        appropriate technical and organisation measures to protect your personal data, we
                        cannot guarantee the security of your personal data transmitted to our website; any
                        transmission is at your own risk. Once we have received your personal data, we will use
                        strict procedures and security features to try to prevent unauthorised access.
                      </li>
                    </ol>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>Storage and deletion of personal data</h2>
                    <ol className='privacy__ordered-list--level-2'>
                      <li>
                        Any personal data that we process will be deleted from our systems once we have
                        completed the purpose for which we were processing the personal data. In some cases,
                        the purpose for which we are processing your personal data will last for a considerable
                        period (for example, if you are a long term customer of ours, we will need to store your
                        data until our relationship with you comes to an end).
                      </li>
                      <li>
                        We will determine the period for which we need to retain your data, acting reasonably,
                        and taking into consideration a number of factors such as your relationship with us, your
                        engagement with us, and the fulfilment of contracts we have with you. For email
                        communication, this means we store your email address until you withdraw consent by
                        unsubscribing or deleting your account, which can be done at any time.
                      </li>
                      <li>
                        We may need to retain your personal data where this is necessary to comply with our
                        legal or regulatory obligations, or to protect the vital interest or the vital interests of
                        another natural person.
                      </li>
                      <li>
                        <a href='www.oliv.com/employers'>www.oliv.com/employers</a> takes appropriate steps to ensure data privacy and security including
                        through various hardware and software methodologies. However <a href='www.oliv.com/employers'>www.oliv.com/employers</a> (website)
                        cannot guarantee the security of any information that is disclosed online
                      </li>
                    </ol>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>Your rights</h2>
                    <ol className='privacy__ordered-list--level-2'>
                      <li>
                        Under data protection laws you have the following fundamental rights:
                        <ol className='privacy__ordered-list--level-3'>
                          <li>the right to access the personal data we hold about you;</li>
                          <li>the right to have your personal data corrected if there are errors or inaccuracies in it,
                            or your personal data is incomplete;</li>
                          <li>the right to restrict the processing we carry out in relation to your personal data;</li>
                          <li>the right to object to the processing we carry out in relation to your personal data;</li>
                          <li>
                            the right to have the personal data we hold about you provided to you in a useable
                            format;
                          </li>
                          <li>
                            the right to complain to a supervisory authority about how and/or why we are
                            processing your personal data;
                          </li>
                          <li>
                            the right to tell us you no longer consent to us processing your personal data. In
                            practice you will usually agree in advance to us using your personal data for
                            marketing purposes and if you no longer wish us to use your personal data for
                            marketing purposes, you can opt out of receiving such marketing messages at any
                            time. You can do this either by unsubscribing from the marketing messages we send
                            you or notifying us in writing at <a href='mailto:privacy@oliv.com'>privacy@oliv.com</a>.
                          </li>
                        </ol>
                      </li>
                      <li>
                        <strong>Your choices regarding your information</strong>: ​You have several choices regarding the use
                        of your personal data:
                        <ol className='privacy__ordered-list--level-3'>
                          <li>
                            <strong>Email communications</strong>:​ We will periodically send you free newsletters and emails
                            that directly promote the use of our Website or the service. When you receive
                            newsletters or promotional communications from us, you may indicate a preference
                            to stop receiving further communications from us and you will have the opportunity
                            to “opt-out” by following the unsubscribe instructions provided in the email you
                            receive or by contacting us at privacy@oliv.com. You can also set your email
                            preferences from within your profile on the Website. Despite your indicated email
                            preferences, we may send you service related communications, including notices of
                            any updates to our service, terms of use or privacy policy.
                          </li>
                          <li>
                            <strong>Profile visibility</strong>:​ As an essential element of our service, most of the personal data
                            you explicitly provide to us when you register or update your profile is displayed on
                            your profile. In order for your profile to be made public, you must go to the
                            preferences section of your profile and then update your profile visibility. By default,
                            your profile is not for public viewing.
                          </li>
                          <li>
                            <strong>Changing or deleting your personal data</strong>: ​You may change or update any of your
                            personal data in your account by editing your profile within your account. You may
                            request deletion of your personal data by us, and we will use commercially
                            reasonable efforts to honor your request, but please note that we may be required to
                            keep such information and not delete it. In this case you will be notified in writing of
                            the reason why we are not able to honor your deletion request, and be provided with
                            a suggested date of when your request will be implemented. When we delete any
                            information, it will be deleted from the active database, but may remain in our
                            archives. We may also retain your information for fraud prevention or similar
                            purposes.
                          </li>
                        </ol>
                      </li>
                      <li>
                        You have the right to obtain from us more information about how we process your
                        personal data, as well as a copy of the written personal data which we have stored about
                        you. This includes written data that you have voluntarily added to the Website in order to
                        create your profile or account, that you can export from your account at any time. In
                        addition to this, you can request for access to media files or chat transcripts which will be
                        provided to you within 3 to 5 working days from the receipt of written request. You also
                        have the right to correct, update or delete your personal data in our records, as well as to
                        report any misuse of your personal data.
                      </li>
                      <li>
                        Our Website may contain links to other websites of interest. We do not have control over
                        any external websites and once you use any of these links to leave our Website we
                        cannot be responsible for the protection and privacy of any information which you
                        provide whilst visiting external sites. Such sites are not governed by this privacy
                        statement. You should exercise caution and look at the privacy statement applicable to
                        the website in question.
                      </li>
                    </ol>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>Cookies</h2>
                    <ol className='privacy__ordered-list--level-2'>
                      <li>
                        Cookies are text files that are set by our Website into the browser on your computer or
                        mobile device. The next time you visit, those cookies allow us to recognise your device.
                        We use cookies on our Website mostly for analytical reasons: to analyse visitor behaviour
                        (e.g. which pages are viewed often) in order to optimise the usability of the Website.
                      </li>
                      <li>
                        We make use of the following third party tools which use cookies. We use Google Tag
                        Manager to implement third party tools. We use the Facebook pixel, Twitter pixel,
                        LinkedIn pixel and Google AdWords Remarketing pixel in order to measure conversion of
                        our advertisements on social media. We use AdRoll to re-market users across the
                        internet. For general website analytics we use Google Analytics. For website usage
                        analytics we make use of Segment, MixPanel, Fullstory, Amplitude, Intercom and
                        HubSpot. We use YouTube and Vimeo to display videos. The chat functionality is
                        provided by Intercom and Twilio and uses functional cookies to remember earlier
                        conversations.
                      </li>
                      <li>
                        You can use the settings within your browser to control the cookies that are set on your
                        computer. To learn more, please look at the cookie settings available in your specific
                        web browser(s): <a href='https://support.google.com/chrome/answer/95647' target='_blank'>Google
                        Chrome</a>
                        &nbsp;- <a href='https://support.mozilla.org/en-US/kb/enable-and-disable-cookies-website-preferences' target='_blank'>Firefox</a>
                        &nbsp;- <a href='https://support.microsoft.com/help/17442/windows-internet-explorer-delete-manage-cookies' target='_blank'>Internet Explorer</a>
                        &nbsp;- <a href='https://support.apple.com/guide/safari/manage-cookies-and-website-data-sfri11471/mac' target='_blank'>Safari</a>.
                        You may also visit <a href='http://youronlinechoices.eu/' target='_blank'>youronlinechoices.eu</a> for
                        details on how to remove cookies of specific parties and for further information on cookies.
                      </li>
                      <li>
                        By continuing to use our Website, you agree to the placement of cookies on your device.
                        If you choose not to receive our cookies, we cannot guarantee that your experience will
                        be as fulfilling as it would otherwise be.
                      </li>
                    </ol>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>Changes to our privacy policy</h2>
                    <div className='privacy__paragraph'>
                      Any changes we make to our privacy policy in the future will be posted on this page and will be
                      effective immediately upon posting. Please check back frequently to see any updates or changes
                      to our privacy policy. By continuing to access our Website, you agree to be bound by the revised
                      statement.
                    </div>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>Information about us</h2>
                    <div className='privacy__paragraph'>
                      This privacy policy relates to Oliv LCC. Oliv LLC is registered under Sharjah Media City under
                      License number 2008378.01, having its registered office at 1101, International Business Tower,
                      Business Bay, Dubai, United Arab Emirates.
                    </div>
                    <div className='privacy__paragraph'>
                      We can be contacted by post at the address stated above, or by email at <a href='mailto:team@oliv.com'>team@oliv.com</a>.
                    </div>
                  </li>
                  <li>
                    <h2 className='privacy__subtitle'>Any questions?</h2>
                    <div className='privacy__paragraph'>
                      If you have any questions, comments or concerns about how we handle personal data or if you
                      wish to make use of your rights described above, please contact us at <a href='mailto:privacy@oliv.com'>privacy@oliv.com</a>.
                    </div>
                  </li>
                </ol>
              </div>
            </div>
          </div>
        </Container>
      </div>
    )
  }
}

export default Privacy



