import { TermsAndConditionsEn, TermsAndConditionsAr, TermsAndConditionsFr } from 'Shared/layouts/TermsAndConditions'
import LanguageService from 'Shared/services/LanguageService'

const filesMap = {
  'en': TermsAndConditionsEn,
  'ar': TermsAndConditionsAr,
  'fr': TermsAndConditionsFr
}

export default filesMap[LanguageService.userLanguage]



